@tailwind base;
/** Colors */
body {
  @apply font-sans;
}

.brand {
  @apply text-3xl font-medium text-indigo-500;
}

/* optional reset for presentation */
* {
  text-decoration: none;
  font-size: 20px;
  box-sizing: content-box;
}

.container,
.banner {
  max-width: 56rem !important;
}

.purple {
  color: #4f2484;
}

@media only screen and (min-width: 640px) {
  .banner {
    max-width: 0 !important;
  }
}
@tailwind components;
@tailwind utilities;
.banner {
  min-height: 85px;
  border-bottom: #f0f0f0 solid 1px;
  padding-bottom: 1rem;
}

.button {
  display: inline-block;
  padding: 0.75rem 1.25rem;
  border-radius: 10rem;
  color: #4f2484;
  font-size: 0.6rem;
  transition: all 0.4s;
  position: relative;
  overflow: hidden;
  min-width: 178px;
  font-weight: bold;
  z-index: 1;
}
.button::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: solid 3px #2ce6ae;
  border-radius: 10rem;
  z-index: -2;
}
.button::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background-color: #2ce6ae;
  transition: all 0.3s;
  z-index: -1;
}
.button:hover::before {
  width: 100%;
}

.block-three-columns .triangles {
  width: 100vw;
  overflow: hidden;
}
.block-three-columns .triangles-wrapper {
  background-color: #baffff;
}
.block-three-columns #right-triangle {
  width: 0;
  height: 0;
  border-left: 100vw solid #baffff;
  border-top: 50vh solid transparent;
  border-bottom: 0 solid transparent;
}
.block-three-columns #left-triangle {
  background-color: #baffff;
  width: 0;
  height: 0;
  border-right: 100vw solid #baffff;
  border-left: 100vw solid #baffff;
  border-top: 50vh solid transparent;
  border-bottom: 50vh solid transparent;
}
.block-three-columns #down-triangle {
  width: 0;
  height: 0;
  border-left: 100vw solid #baffff;
  border-top: 0 solid transparent;
  border-bottom: 50vh solid transparent;
}
.block-three-columns .image-outer {
  padding: 40px;
  justify-content: center;
  height: 60px;
  align-content: center;
}
.block-three-columns .image-wrapper {
  display: flex;
  height: auto;
  justify-content: center;
  align-self: flex-start;
}
.block-three-columns img {
  width: 80%;
}

@media only screen and (min-width: 640px) {
  .block-steps .outer {
    box-sizing: border-box;
    padding: 80px;
  }
}
.block-steps .number {
  border: 4px solid #baffff;
  border-radius: 50%;
  padding: 4px 12px;
}